#contact {
  background: url('../images/backgrounds/contact.jpg');
  background-size: cover;
  background-color: #eff8f9;
  flex: 1;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;
  height: 100%;

  @media @mobile, @tablet{
    padding: 35px 20px 20px 20px;
    background-position: -240px 0;
    background-repeat: no-repeat;
    background-color: #fff;
  }


  .left-side {
		display: flex;
		flex: 1;
		flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }


  .content {
  	height: 100%;
  	display: flex;
  	flex-direction: column;


    .heading {
      display: flex;

      .counterweight {

        &:first-child  {
          flex: 1.5;
        }

        &:last-child {
          flex: 1;
        }



        @media @mobile, @tablet {
          display: none;
        }
      }

      .headline {
        flex: 4;
        color: #249fbe;
        margin-top: 0;
        font-size: 80.5px;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        margin-bottom: 0;
        margin-top: 140px;
        text-align: center;

        @media @mobile, @tablet {
          font-size: 100%;
          text-align: left;
          padding-left: 20px;
          margin-top: 80px;
        }
      }
    }

    .main-content {
      align-items: center;
      padding-top: 5%;
  		display: flex;
      margin-left: 30px;
	  	flex-direction: column;
			// align-self: stretch;
			flex: 1;

      @media @mobile, @tablet {
        margin-left: 0;
      }

  		.divider {
  			flex: 1;
  		}

			.content {
				display: flex;

        @media @mobile, @tablet {
          position: relative;
          left: 50px;
        }

        & > * {
          flex: 1;
        }

				.left-side {
          @media @mobile, @tablet {
            display: none;
          }
				}

				.right-side {
          margin-left: 400px;

          @media @mobile {
            margin: 0;
          }
          .contact-list {
            display: flex;
            flex-direction: column;

            li {
              margin: 5px 0;
              display: flex;
              align-items: center;

              @media @mobile {
                margin: 2.5px 0;
              }

              i.fa {
                color: #303030;
                border: 1px solid #e5e5e5;
                background: #fff;
                border-radius: 50%;
                padding: 8px;
                width: 48px;
                height: 48px;
                font-size: 30px;

                @media @mobile, @tablet {
                  width: 26px;
                  height: 26px;
                  font-size: 16px;
                }

                &.fa-envelope {
                  padding: 8px;

                  @media @mobile, @tablet {
                    padding: 4px;
                  }
                }
                &.fa-linkedin {
                  padding: 7px 11px;

                  @media @mobile, @tablet {
                    padding: 3px 6px;
                  }
                }
                &.fa-facebook {
                  padding: 9px 13px;

                  @media @mobile, @tablet {
                    padding: 5px 7px;
                  }
                }
                &.fa-instagram {
                  padding: 8px 10px;

                  @media @mobile, @tablet {
                    padding: 4px 6px;
                  }
                }
                &.fa-twitter {
                  padding: 9px 9px;

                  @media @mobile, @tablet {
                    padding: 5px 6px;
                  }
                }
              }

              a {
                margin-left: 30px;
                font-family: 'Proxima Nova';
                color: #009ec0;
                font-size: 24px;
                text-transform: uppercase;
                text-decoration: none;
                font-weight: bold;

                @media @mobile, @tablet {
                  font-size: 8px;
                  margin-left: 15px;
                }
              }
            }
          }

          .qr {
            margin-top: 50px;

            @media @mobile, @tablet {
              margin-top: 15px;

              img {
                width: 30%;
              }
            }
          }
				}
			}


  	}
  }
}

@media @WXGA-display {
    #contact {
      background-position: 0 -12px;

      .content {

        .main-content {
          padding-top: 4.3%;
          margin-left: 9px;

            .content {

              > * {
                flex: 0;
              }
                .right-side {
                    .qr {
                      margin-top: 25px;

                      img {
                        width: 105px;
                        margin-left: -6px;
                      }
                    }
                    .contact-list li {

                      i.fa {
                        width: 34px;
                        height: 34px;
                        font-size: 20px;

                        &.fa-envelope {
                         padding: 6px;
                        }
                        &.fa-linkedin {
                          padding: 5px 8px;
                        }
                        &.fa-facebook {
                          padding: 7px 9px;
                        }
                        &.fa-instagram {
                         padding: 6px 7px;
                        }
                        &.fa-twitter {
                          padding: 6px;
                        }
                      }

                      a {
                        font-size: 16px;
                        margin-left: 10px;
                      }
                    }
                }
            }
        }

        .heading {
          .counterweight {
            &:first-child {
              flex: 2.4;
            }
          }

          .headline {
            margin-top: 77px;
            font-size: 53.5px;
          }
        }
      }

      .footer {

        .copyright {
          margin-left: -16px;
          margin-top: -17px;
          font-size: 12px;
        }

        .bottom {
          .box {
            &:first-child {
              margin-right: 8px;
            }
            &:nth-child(2) {
              margin-right: 8px;
            }
            &:nth-child(3) {
              margin-right: 17px;
            }
            &:nth-child(4) {
              margin: 0;
            }
          }
        }

        .cta {
          font-size: 12px;
          text-transform: uppercase;

          padding-left: 25px;
          margin-right: 35px;
        }
      }
    }
}
