

// Normalize Styles
@import (inline) 'node_modules/normalize.css/normalize.css';
@import (inline) 'node_modules/font-awesome/css/font-awesome.min.css';
@import (inline) 'node_modules/perfect-scrollbar/dist/css/perfect-scrollbar.css';
// @import (inline) 'node_modules/animate.css/animate.min.css';


// Import Modules
@import '../_modules/header/header';
@import '../_modules/footer/footer';
@import '../_modules/horizontal-nav/horizontal-nav';

// Import Peripheral Styles
@import './mixins/variables';
@import './mixins/fonts';
@import './mixins/mixins';
@import './mixins/animate';
@import './mixins/page-transitions';


body {
    background: #edf9f9;

    &.no-overflow {
      overflow: hidden;
    }
}

html,body {
  height: 100%; margin: 0px; padding: 0px;
}

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

//Resting ul styles
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

#content-wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  overflow: hidden;
  position: absolute;
  min-height: 100%;
  width: 100%;
  height: 100%;
}


//Import Styles
@import 'index';
@import 'contact';
@import 'about';
@import 'blog';
@import 'article';
@import 'portfolio';
@import 'expertise';

@import 'scrollbar';


a {
  color: #1f1f1f;

  &:hover {
    color: #ff7d00;
  }
}
