#about {
  height: 100%;
  background: url('../images/backgrounds/about.jpg');
  background-size: cover;
  background-color: #ecf8f9;

  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;

  @media @mobile {
    padding: 35px 20px 20px 20px;
    background-position: -130px -30px;
    background-position: -35vw -5vh;
    background-repeat: no-repeat;
    background-size: 250%;
    background-color: #e6f1f5;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

  }

  .content {
    height: 100%;
    display: flex;
    min-height: 100%;
    display: flex;
    padding-top: 15%;

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      & > * {
        // display: flex;
        // flex: 1
      }

      .page-content {
        display: flex;

        & > * {
          flex: 1;
        }

        @media @mobile {
          .left-side {
            display: none;
          }
        }

        .right-side {
          display: flex;
          flex-direction: column;

          .spans {
            display: flex;
            flex-direction: column;
            height: 250px;
            flex-direction: column;
            justify-content: space-between;
            width: 600px;

            @media @desktop, @laptop {
              margin-left: 152px;
              margin-top: 30px;

              @-moz-document url-prefix() {
                margin-top: 150px;
              }
            }

            @media @laptop {
              height: 170px;
            }

            @media @WXGA-display {
              margin-left: 134px;
              margin-top: 11px;
              width: 445px;
              height: 182px;
            }

            @media @mobile {
              flex: 4;
              height: 250px;
              justify-content: flex-end;
              margin: 0;
            }

            span {
              color: #525a5d;
              font-size: 24px;
              line-height: 33px;
              font-family: 'Proxima Nova';

              @media @WXGA-display {
                font-size: 16px;
                line-height: 22px;
                letter-spacing: 0.025em;
              }

              @media @laptop {
                font-size: 20px;
              }

              @media @mobile {
                font-size: 12px;
                width: ~"calc(100% - 300px)";
                margin-top: 20px;
              }

              &:first-letter {
                text-transform: uppercase;
              }
            }
          }

          section.heading {
            color: #249fbe;
            font-size: 80.5px;
            text-transform: uppercase;
            font-family: 'Brandon Black';
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            flex: 2;
            align-items: center;
            height: 400px;
            justify-content: center;

            @media @WXGA-display {
              height: 251px;
              font-size: 55.5px;
            }

            @media @mobile {
              flex: 1;
              font-size: calc(26px + 2vw);
              width: calc(100vw-5px);
              align-items: flex-end;
            }

            div {
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              margin-left: 70px;
              line-height: 1.2em;

              @media @WXGA-display {
                margin-left: 67px;
                margin-top: -4px;
              }

              @media @mobile {
                margin: 0;
                line-height: normal;
                flex-direction: column;
                line-height: 1;

                &:first-child {
                  margin-right: 5px;
                }
              }
            }
          }
        }
      }

      .divider {
        flex: 1;
      }

      .heading {
        color: #249fbe;
        font-size: 80.5px;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        display: flex;
        flex-direction:column;
        align-items:flex-end;
        flex: 2;

        @media @mobile, @tablet {
          justify-content: flex-end;
          font-size: 250%;
        }

        .row {
          display:flex;
          flex-direction:row;
          width:50%;

          @media @mobile, @tablet {
            width: 100%;
          }

          .box {
            flex: 1;

            &.big-flex {
              flex: 3;

              @media @mobile {
                flex: 0;
              }

              @media @tablet {
                flex: 10;
              }
            }

            &.text {
              flex: 10 0 auto;
            }

            &.align-right {
              text-align:right;
            }
          }
        }
      }

      .footer {
        display: flex;
        align-items: center;

        @media @WXGA-display {
          flex: 2;
        }

        .copyright {
          flex: 3;
          font-family: 'Proxima Nova';
          font-size: 14px;
          color: #9db7c1;
          letter-spacing: 0.025em;

            @media @WXGA-display {
              margin-left: -16px;
              margin-top: 0;
              font-size: 12px;
            }
        }

        .copyright-mobile {
          display: none;
        }

        .social {
          flex: 1;
          display: flex;
          align-items: center;

          .box {
            margin: 8px;

            @media @WXGA-display {
              &:first-child {
                margin-right: 8px;
              }
              &:nth-child(2) {
                margin-right: 8px;
              }
              &:nth-child(3) {
                margin-right: 17px;
              }
              &:nth-child(4) {
                margin: 0;
              }
            }

            i {
              color: #1d9dbd;
              font-size: 15px;
            }
          }

        }
        .cta {
          padding-left: 40px;
          font-family: 'Brandon Bold';
          font-size: 14px;

            @media @WXGA-display {
              font-size: 12px;
              padding-left: 25px;
              margin-right: 35px;
            }

          a {
            text-decoration: none;
            color: #1d9dbd;
          }
        }

        ul.mobile-bottom-navigation {
          display: none;
        }
      }
    }
  }


}

.side-nav {
  position: absolute;
  top: 0;
  right: 40px;
  height: 100%;
  border-left: 1px solid #cadbe0;
}


@media @WXGA-display {
    #about {
        .content .main-content {
          .page-content .right-side {
            .spans {
                margin-left: 134px;
                margin-top: 11px;
                width: 445px;
                height: 182px;

                span {
                  font-size: 16px;
                  line-height: 22px;
                  letter-spacing: 0.025em;
                }
            }

            section.heading {
              height: 251px;
              font-size: 55.5px;

              div {
                margin-left: 67px;
                margin-top: -4px;
              }
            }
          }

          .footer {
            flex: 1;

            .copyright {
              margin-left: -16px;
              margin-top: 6px;
              font-size: 12px;
            }

            .bottom {
              .box {
                &:first-child {
                  margin-right: 8px;
                }
                &:nth-child(2) {
                  margin-right: 8px;
                }
                &:nth-child(3) {
                  margin-right: 17px;
                }
                &:nth-child(4) {
                  margin: 0;
                }
              }
            }

            .cta {
              font-size: 12px;
              text-transform: uppercase;

              padding-left: 25px;
              margin-right: 35px;
            }
          }

        }
    }
}
