#index {
	height: 100%;
  background: url('../images/backgrounds/index.jpg');
  background-size: cover;
  background-color: #eff8f9;

  padding: 40px 75px 25px 50px;

  display: flex;
  flex-direction: column;

  .left-side {
		display: flex;
		flex: 1;
		flex-direction: column;
    justify-content: space-between;
		height: 100%;
  }

  .content {
  	height: 100%;

  	display: flex;
  	// flex-direction: column;
  	padding-top: 10vh;
  	& > * {
  		// flex: 1
  	}

  	.main-content {
			height: 100%;
  		display: flex;
	  	flex-direction: column;
			align-items: stretch;
			flex: 1;
  		& > * {

  			// display: flex;
	  		// flex: 1
	  	}



  		// .top {

  		// 	display: flex;
			 //  flex-flow: column;
				// height: 100%;
			 //  flex: 3;
			 //  // padding-top: 10%;

				// .headline {
				// 	// position: relative;
				// 	// top: 0;
				// 	// right: 0;



				// 	display: flex;
	  	// 		flex-direction: row;
  		// 		justify-content: flex-end;


  		// 		// .first {
  		// 		// 	width: 100%;
  		// 		// }
  		// 		// .break {
  		// 		// 	// width: 100%;
  		// 		// }
  		// 		// .second {
  		// 		// 	width: 100%;
  		// 		// }
				// }
  		// }

  		.divider {
  			flex: 1;
  		}

  		.heading {
  			color: #249fbe;
				font-size: 80.5px;
				letter-spacing: 0.025em;
				text-transform: uppercase;
				font-family: 'Brandon Black';

			  display: flex;
			  flex-direction:column;
			  align-items:flex-end;
		    flex: 2;

			  .row {
			    display:flex;
			    flex-direction:row;
			    width:50%;

					margin-top: 30px;
					height: 55px;

					&:first-child {
						margin-top: 100px;
						padding-left: 110px;

						@media @mobile {
							margin-top: 50%;
							padding: 0;
						}
					}

			    .box {
			      flex: 1;

			      &.big-flex {
			      	flex: 3;
			      }

			      &.text {
			        flex: 10 0 auto;
			      }

			      &.align-right {
			        text-align:right;
			      }
			    }
			  }
			}

  		.footer {

  			display: flex;
				align-items: center;

				.copyright {
					flex: 3;
					font-family: 'Proxima Nova';
					font-size: 14px;
					letter-spacing: 0.025em;
					color: #9db7c1;
				}

				.copyright-mobile {
					display: none;
				}

  			.social {
					flex: 1;

  				display: flex;
					align-items: center;

					.box {
						margin: 8px;

						i {
							color: #1d9dbd;
							font-size: 15px;
						}
					}

  			}
  			.cta {
  				padding-left: 40px;
					font-family: 'Brandon Bold';
					font-size: 14px;

					a {
						text-decoration: none;
						color: #1d9dbd;
					}
  			}

  			ul.mobile-bottom-navigation {
  				display: none;
  			}
  		}
  	}
  }


}

.side-nav {
	position: absolute;
	top: 0;
	right: 40px;
	height: 100%;
	border-left: 1px solid #cadbe0;
}

/*Media*/

@media @fullHD-display {
	#index {
		background-position: 0 -5px;

		.content .main-content {
			.heading {

				.row {
					&:first-child {
						margin-top: 77px;
	    				padding-left: 112px;
					}

					&:nth-child(2n) {
						margin-top: 28px;
    					margin-right: 31px;
					}
				}
			}

			.footer {
				flex: 0.3;

				.copyright {
					font-size: 12px;
				}

				.bottom {
					.box {
						&:first-child {
							margin-right: 12px;
						}
						&:nth-child(2) {
							margin-right: 10px;
						}
						&:nth-child(3) {
							margin-right: 21px;
						}
						&:nth-child(4) {
							margin: 0;
						}
					}
				}

				.cta {
					font-size: 11px;
					text-transform: uppercase;
					margin-right: 76px;
				}
			}
		}
	}
}

@media @WXGA-display {
	#index {
		background-position: 2px -18px;

		.content .main-content {

			.heading {
				font-size: 57.43px;

				.row {
					&:first-child {
						margin-top: 37px;
	    				padding-left: 81px;
					}

					&:nth-child(2n) {
						margin-top: 4px;
	    				margin-right: 7px;
					}
				}
			}

			.footer {
				flex: 0.35;

				.copyright {
					margin-left: -17px;
					font-size: 12px;
				}

				.bottom {
					.box {
						&:first-child {
							margin-right: 8px;
						}
						&:nth-child(2) {
							margin-right: 8px;
						}
						&:nth-child(3) {
							margin-right: 17px;
						}
						&:nth-child(4) {
							margin: 0;
						}
					}
				}

				.cta {
					font-size: 12px;
					text-transform: uppercase;

					padding-left: 25px;
					margin-right: 35px;
				}
			}
		}

	}
}

