.navigation-main {
    position: absolute;
    z-index: 3;
    width: 100%;
    padding: 36px 100px 20px 51px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: flex-end;

    @media @mobile {
      z-index: 2;
    }

    .logo {
      width: 110px;
      height: 30px;
      display: block;
      background: url('../images/logo/logo.png') no-repeat 0 0;
      background-size: 100% 100%;
      animation: pulse 2s 1;



      // &:before {
      //   content: ' ';
      //   width: 100px;
      //   display: block;
      //  animation: pulse 2s 1;
      //   height: 6px;
      // }

      &.nav-opened ~ .logo-overlay {
        // background: #fff;
        // animation: pulse-opened 2s 1;
      }

      @keyframes pulse {
        0% {
          background: url('../images/logo/logo.png') no-repeat 0 0;
          background-size: 100% 100%;
        }
        25% {
          background: url('../images/logo/logo-no-button.png') no-repeat 0 0;
          background-size: 100% 100%;
        }
        75% {
          background: url('../images/logo/logo.png') no-repeat 0 0;
          background-size: 100% 100%;
        }
        100% {
          background: url('../images/logo/logo-no-button.png') no-repeat 0 0;
          background-size: 100% 100%;
        }
      }

      @keyframes pulse-opened {
        0% {
          background-color: #fff;
        }
        25% {
          background: none;
        }
        75% {
          background-color: #fff;
        }
        100% {
          background: none;
        }
      }
    }
    .navigation-bars{
      display: none;

      @media @mobile-portrait {
        margin-left: 10%;
      }
    }

    .languages {
      position: absolute;
      display: flex;
      flex-direction: column;
      right: 59px;
      z-index: 2;

      ul {
        li {
          margin: 5px 0;
          border: 1px solid #cadbe0;
          border-radius: 50%;
          padding: 7px 5px;
          font-size: 10px;
          text-align: center;
          text-transform: uppercase;
          background: #fff;
        }
      }
    }

    .languages-mobile {
      display: none;
    }

  }


.nav-box {
  position: fixed;
  text-align: center;
  overflow: hidden;
  z-index: -1;
  display: none;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  background: #fff;
  // // background: rgba(0, 0, 0, 0.8);
  // -webkit-transition: all 0.3s ease-in-out;
  // -moz-transition: all 0.3s ease-in-out;
  // -o-transition: all 0.3s ease-in-out;
  // transition: all 0.3s ease-in-out;
  ul {
    position: relative;
    top: 20%;
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    padding: 0;
    height: 50%;
    padding-left: 15px;
  }

  li {
    flex: 1;
    display: flex;
    align-items: center;
    font-family: 'Brandon Black';

    &:first-child {
      margin-bottom: 20px;
    }

    h1 {
      color: #1c9cbc;
      font-size: 3em;
    }

    a {
      flex: 1;
      text-decoration: none;
      font-size: 1.5em;
      text-transform: uppercase;
      color: #000;

      &:hover, &.active {
        color: #ff7d00;
      }
    }
  }
}

#toggle-nav-label {
  color: #000000;
  font-size: 16px;
  cursor: pointer;
  z-index: 500;
}

#toggle-nav {
  display: none;
}


#toggle-nav:checked  {

  & ~ .nav-box {
    display: block;
    z-index: 400;
  }

}

#toggle-nav:checked ~ .nav-box ul {
  // -webkit-transform: scale(1);
  // -moz-transform: scale(1);
  // -ms-transform: scale(1);
  // transform: scale(1);
}

#toggle-nav:checked ~ #toggle-nav-label {
  background: #fff;
  color: rgba(0, 0, 0, 0.8);
}

  ul.nav {
    display: flex;
    list-style: none;
    padding: 0;
    li {
      a {
        padding: 0 26px;
        color: #131313;
        font-family: 'Brandon Black';
        text-transform: uppercase;
        font-size: 14px;
        opacity: .6;
        text-decoration: none;
        letter-spacing: 0.025em;

        &:hover, &.active {
          color: #ff7d00;
          opacity: 1;
        }
      }
    }
  }

/*Media*/
@media @fullHD-display {
  .navigation-main{
    padding-right: 116px;

    ul.nav li a {
      padding: 0;
      margin-right: 33px;
    }
  }
}

@media @WXGA-display {
  .navigation-main{
    padding: 13px 78px 20px 35px;

    .logo {
      width: 94px;
      height: 24px;
    }

    ul.nav li a {
      font-size: 12px;
      padding: 0;
      margin-right: 28px;
    }
  }
}

@media @mobile,
@tablet {
 .navigation-main {
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    padding: 40px 30px 25px 20px;
    .logo {
      width: 100px;
      height: 18px;
      background: url('../images/logo/logo.png') no-repeat 0 0;
      background-size: contain;
      animation: none;
    }

    .navigation-bars {
      flex: 1;
    }


    .left-side {
      z-index: 500;
      flex: 1;
    }

    .navigation-bars {
      display: flex;
      flex: 1;



      i {
         align-self: center;
         font-size: 24px;
      }
    }

    ul.nav {
      display: none;
      flex-direction: column;

      &.open {
        display: flex;
        position: absolute;
        margin: 0 auto;
        background: rgb(255,255,255,.3);
        width: 100%;
        left: 0;
        text-align: center;
        height: 100%;
        justify-content: center;

      }
    }

    .languages-mobile {
      display: block;
      z-index: 500;
      align-self: flex-end;

      ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
          color: #1d9cbd;
          background: #eff7f9;
          border: 2px solid #cadbe2;
          border-radius: 50%;
          padding: 3px;
          text-transform: uppercase;
          font-size: 11px;
          font-family: 'Brandon Black';
        }
      }
    }
  }
}
