.navigation-horizontal {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  position: absolute;
  right: 8px;
  z-index: 3;
  top: 200px;

  ul.numbers {
    display: flex;
    list-style: none;
    justify-content: flex-end;
    align-items: center;
    right: 30px;
    position: relative;
    font-size: 20px;
    letter-spacing: 0.025em;

    li {
      padding: 20px;
      transform: rotate(270deg);
      width: 10px;
      height: 10px;
      a {
        text-decoration: none;
        color: #a8d0da;
        font-family: 'PFDinTextPro-Regular';

        position: relative;
        right: 10px;      //instead of top, since our element is rotated

        &:hover, &.active {
          color: #1d9dbd;
        }
      }
    }

    li.leading-line {
      width: 140px;
      transform: rotate(0deg);

      &:before {
        content: ' ';
        background: #cbdbe0;
        height: 1px;
        width: 100px;
        display: block;
      }
    }
  }

  ul.arrows {
    display: flex;
    list-style: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    li {

      background: #249fbe;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;

      &.forward {
        width: 65px;
        height: 65px;
      }

      &.back {
        width: 35px;
        height: 35px;
      }

      &.divider {
        flex: 1 1 10px;
        background: none;
      }



      a {
        color: #fff;
        text-decoration: none;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &:hover > i {
          animation: arrow-shake .6s 1;
        }
      }


      @keyframes arrow-shake {
        0% {
          -webkit-transform: translate3d(-1.5px, 0, 0);
          transform: translate3d(-1.5px, 0, 0);
        }


        50% {
          -webkit-transform: translate3d(1.5px, 0, 0);
          transform: translate3d(1.5px, 0, 0);
        }

        100% {
          -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
        }
      }
    }
  }
}

/*Media*/

@media @fullHD-display {
  .navigation-horizontal {
    right: 38px;

    ul.arrows li {
      &.forward {
        width: 66px;
        height: 66px;
      }

      &.back {
        width: 31px;
        height: 31px;
      }
    }
    ul.numbers {
      font-size: 18px;

      li {

        &.leading-line {
          width: 188px;

          &:before {
            width: 142px;
          }
        }
      }
    }
  }

  #content-wrapper .side-nav {
    right: 71px;
  }
}

@media @WXGA-display {
  .navigation-horizontal {
    top: 127px;
    right: 26px;

    ul.arrows li {
      &.forward {
        width: 58px;
        height: 58px;
      }

      &.back {
        width: 27px;
        height: 27px;
        margin-top: -2px;
        margin-right: 3px;
      }
    }

    ul.numbers {
      font-size: 14px;
      right: 16px;

      li {
        padding: 8px;
        margin: 0 13px 0 0;

        &.leading-line {
          width: 100px;

          &:before {
            width: 100px;
          }
        }

        a {
          right: 4px;
        }
      }
    }
  }

  #content-wrapper .side-nav {
    right: 56px;
  }
}

@media @mobile,
@tablet {
  .navigation-horizontal, .side-nav {
    display: none;
  }
}

.side-nav {
  z-index: 1;
}
