#blog {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;
  height: 100%;



  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    // padding-top: 10vh;
    padding: 60px 0 0 0;

    @media @mobile {
      padding: 0;
    }

    & > * {
      // flex: 1
    }

    .headline {
      color: #249fbe;
      margin-top: -25px;
      font-size: 80.5px;
      text-transform: uppercase;
      font-family: 'Brandon Black';
      margin-bottom: 0;
      // margin-top: 20px;

      @media @mobile {
        margin-top: 60px;
      }
    }

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;
      height: 100%;

      & > * {
        // display: flex;
        // flex: 1
      }

      .divider {
        flex: 1;
      }



      & > .content {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: center;
        overflow: hidden;
        position: relative;
        height: 35em;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0;

        article {
          display: flex;
          width: 80%;
          align-items: flex-start;
          flex-shrink: 0;

          &:not(:first-child) {
            padding: 60px 0;

            @media @mobile {
              padding: 0 15px;
            }
          }
          // .image {
          //   img {

          //   }
          // }

          .content {
            display: flex;
            margin-left: 130px;
            padding: 0;

            h4 {
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 34px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;
            }

            p {
              font-family: 'Proxima Nova';
              color: #525a5d;
            }

            .footer {
              display: flex;
              font-family: 'Proxima Nova';
              font-size: 17px;
              flex: 1;
              justify-content: space-between;
              width: 200px;
              align-self: baseline;

              @media @mobile {
                justify-content: flex-start;
              }

              .date {
                color: #b1ccd2;
              }

              a {
                color: #1f1f1f;

                @media @mobile {
                  margin-left: 10px;
                }

                &:hover {
                  color: #ff7d00;
                }
              }
            }
          }
        }
      }
    }
  }
}


@media @mobile,
@tablet {
  #blog {
    background-size: cover;
    padding: 0px;
    // padding: 35px 20px 20px 20px;

    .headline {
      font-size: 250% !important;
      padding: 80px 0 10px 10px;
    }

    .content {
      .main-content {
        padding: 0;

        .headline {
          padding: 0 0 10px 10px;
        }

        .content {

          article {
            margin-bottom: 30px;
            width: 100%;
            padding: 0 15px;

            .image {
              flex: 1 !important;

              img {
                width: 25vw;
                height: 25vw;
              }
            }

            .content {
              flex: 1 !important;
              margin: 0 !important;


              h4 {
                font-size: 14px;
              }

              p {
                font-size: 12px;
              }

              .footer {
                flex-direction: row;
                font-size: 12px;
              }
            }
          }
        }


      }
    }

    footer {
      padding: 35px 20px 20px 20px;
    }
  }
}

@media @tablet {
  .heading {
    justify-content: flex-end;
    font-size: 250%;

    .row {
      width: 100%;

      .box {
        &.big-flex {
          flex: 10 !important;
        }
      }
    }
  }
}

@media @mobile {
  .heading {
    justify-content: flex-end;
    font-size: 250%;

    .row {
      width: 100%;

      .box {
        &.big-flex {
          flex: 0 !important;
        }
      }
    }
  }
}
