footer.footer {
  display: flex;
  align-items: center;

  .copyright {
    flex: 3;
    font-family: 'Proxima Nova';
    font-size: 14px;
    color: #9db7c1;
  }

  .copyright-mobile {
    display: none;
  }

  .social {
    flex: 1;
    display: flex;
    align-items: center;

    .box {
      margin: 8px;

      a {
        text-decoration: none;
        color: #1d9dbd;
        font-size: 15px;
      }
    }

  }
  .cta {
    padding-left: 40px;
    font-family: 'Brandon Bold';
    font-size: 14px;
    color: #1d9dbd;
    text-transform: uppercase;

  }

  .mobile-bottom-navigation {
    display: none;
  }
}

@media @mobile,
@tablet {
  .footer {
    flex-direction: column;
    align-items: flex-start !important;
    z-index: 1;
    margin-top: 30px;
    min-height: 50px;

    .bottom {
      display: flex;
      width: calc(100% - 60px);
    }

    .copyright {
      display: none;
    }

    .copyright-mobile {
      order: 1;
      display: block !important;
      font-family: 'Proxima Nova';
      font-size: 14px;
      color: #9db7c1;
    }
    .social {
      order: 0;

      .box {
        a, a:visited {
          text-decoration: none;
          color: #1d9dbd !important;
        }

        &:first-child {
        margin-left: 0;
        }
      }
    }

    .cta {
      display: none;
    }

    .mobile-bottom-navigation {
      padding: 0;
      flex: 1;
      margin: 0;

      display: flex !important;
      list-style: none;
      flex-direction: row;
      align-items: center;

      position: relative;
      left: 70px;

      &:before {
        content: ' ';
        height: 1px;
        width: 90vh;
        top: 2em;
        left: -50px;
        position: absolute;
        background: #cadbe0;
        z-index: -1;
      }

      .divider {
        margin: 0 10px;
        background: none;
      }

      .arrow {
        // flex: 10;
        border-radius: 50%;
        background: #249fbe;
        display: flex;
        align-items: center;
        justify-content: center;

        &.forward {
          width: 65px;
          height: 65px;
        }

        &.back {
          width: 35px;
          height: 35px;
        }

        a {
          color: #fff;
          text-decoration: none;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;

          &:hover > i {
            animation: arrow-shake .6s 1;
          }
        }


        @keyframes arrow-shake {
          0% {
            -webkit-transform: translate3d(-1.5px, 0, 0);
            transform: translate3d(-1.5px, 0, 0);
          }


          50% {
            -webkit-transform: translate3d(1.5px, 0, 0);
            transform: translate3d(1.5px, 0, 0);
          }

          100% {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
          }
        }
      }
    }
  }
}
