#portfolio {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;
  height: 100%;

  @media @mobile {
    z-index: 1;
    padding: 35px 20px 20px 20px;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;
      height: 100%;

      @media @mobile {
        padding: 50px 0px 0px 0;
        height: initial;
        overflow: scroll;
      }



      .divider {
        flex: 1;
      }

      .headline {
        color: #249fbe;
        margin-top: 22px;
        font-size: 80.5px;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        display: flex;
        flex-direction:column;
        align-items:flex-start;
        flex: 2;
        margin-left: 238px;
        // align-self: flex-end;

        @media @mobile {
          font-size: calc(10vw + 1px);
          padding: 0 0 50px 0px;
          align-items: flex-start;
          margin: 0 0 -20px 0;
        }
      }

      section.content {
        // display: flex;
        // justify-content: space-around;
        // flex-direction: column;
        // align-items: center;
        // overflow: auto;
        flex: 1 0 auto;
        justify-content: space-around;
        align-items: center;
        //
        // position: absolute;
        // width: 100%;
        // top: 0;
        // left: 0;

        @media @mobile {
          flex-direction: column;
          height: inherit;
        }

        & > div {
          flex: 4;
          z-index: 1;

          @media @mobile {
            min-height: 400px;
          }
        }


        &::before {
          content: ' ';
          display: block;
          background: #cddee3;
          width: 100%;
          height: 1px;
          position: absolute;
          left: 0;
          top: 487px;
          z-index: 1;

          @media @mobile {
            display: none;
          }
        }

        @media @mobile {
          align-items: flex-start;
        }

        ul.portfolios {
          flex: 1;
          display: flex;
          flex-direction: column;
          width: 20%;
          z-index: 1;
          position: relative;
          transition: top .1s linear;

          min-height: 100%;
          margin-top: 440px;
          // margin-top: 540px;
          // margin-top: 34px;
          // margin-top: 145px;
          // z-index: 1;
          // margin-left: 50px;

          @-moz-document url-prefix() {
            margin-top: 210px;
          }

          @media @mobile {
            display: none;
          }


          li {
            z-index: 1;
            margin-top: 20px;

            a {
              color: #000;
              font-family: 'Brandon Black';
              font-size: 14px;
              text-decoration: none;
              text-transform: uppercase;

              &:hover {
                color: #ff7d00;
              }
            }

            &.active {
              a {
                color: #ff7d00;
              }
            }
          }
        }

        .article-holder {
          z-index: 1;
        }

        article {
          display: flex;
          width: 80%;
          flex-direction: column;
          // margin-top: -160px;

          margin-top: -160px;


          // margin-top: 70px;
          flex-shrink: 0;
          flex-direction: row;
          justify-content: space-between;
          z-index: 1;
          display: none;
          transition: all 1s linear;

          // position: relative;
          // transition: all 1s linear;
          // top: 65px;
          // left: 0;
          @-moz-document url-prefix() {
            margin-top: -380px;
          }

          &.active {
            display: flex;
          }

          @media @mobile {
            margin-top: 10px;
            width: 100%;
            flex-direction: column;
          }

          .portfolio-transition {
            // opacity: 1;
            // transition: all .2s linear;
            // &.out {
            //   opacity: 0;
            // }
          }

          .content {
            display: flex;
            margin-left: 22px;
            flex-direction: column;
            height: 371px;
            justify-content: space-between;

            @media @mobile {
              height: 100%;
              margin-left: 0;
              order: 1;
            }

            .image-mobile {
              display: none;

              @media @mobile {
                order: 1;
                align-self: center;
                display: flex;
                flex-direction:column;
                align-items: flex-start;

                img {
                  max-width:100%;
                  max-height:100%;
                }
              }
            }

            div {
              flex: 5 4 50%;
              display: flex;

              @media @mobile {
                flex: 0;
              }

              h4 {
                color: #009ec0;
                font-family: 'Brandon Black';
                font-size: 34px;
                margin: 0;
                align-self: center;
                text-transform: uppercase;

                @media @mobile {
                  margin: 5px 0;
                  font-size: 22px;
                  order: 0;
                }
              }
            }

            p {
              font-family: 'Proxima Nova';
              color: #525a5d;
              flex: 1 16 100%;
              line-height: 1.7;
              width: 560px;

              @media @mobile {
                flex: 0;
                order: 2;
                width: auto;
                font-size: 12px;
              }
            }
          }

          .image {
            align-self: center;
            display: flex;
            flex-direction:column;
            align-items: flex-start;
            margin: 0 auto;

            @media @mobile {
              display: none;
            }

            img {
              max-width:100%;
              max-height:100%;
            }
          }
        }

        .controls {
          display: none;

          @media @mobile {
            display: flex;
            justify-content: center;
            flex: 4;
            align-self: center;

            a.arrow {
              transition: color .2s linear;
              cursor: pointer;
              -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
              height: 30px;
              &:hover > img {
                // filter: ~"grayscale(100%) brightness(0)";
              }
            }
          }
        }
      }
    }
  }
}


