#article {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;
  height: 100%;

  @media @mobile {
    padding: 35px 20px 20px 20px;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    // padding-top: 10vh;
    padding: 60px 0 0 0;

    @media @mobile {
      padding: 0;
    }

    .headline {
      color: #249fbe;
      margin-top: -10px;
      font-size: 38px;
      text-transform: uppercase;
      font-family: 'Brandon Black';
      margin-bottom: 0;
      max-width: 70%;
      line-height: 1;
      padding-left: 50px;

      @media @mobile {
        font-size: calc(5vw + 1px);
        padding: 10px 0;
        align-items: flex-start;
      }
    }

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;
      height: 100%;

      @media @mobile {
        padding: 40px 0 0 0;
      }

      .content {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: flex-start;
        overflow-y: auto;
        overflow-x: hidden;

        article {
          display: flex;
          align-items: flex-start;
          flex-shrink: 1;
          padding: 0;

          @media @mobile {
            padding: 5px 0 0 0;
            flex-direction: column;
          }

          .article-left, .article-right {
            display: flex;
          }

          .date {
            flex: 1;
            color: #b1ccd2;

            @media @mobile {
              display: none;
            }
          }

          .image {
            flex: 2;

            .video-container {
              float: none;
              clear: both;
              width: 100%;
              position: relative;
              padding-bottom: 56.25%;
              padding-top: 25px;
              height: 0;

              iframe {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
              }
            }

            @media @mobile {
              img {
                width: 100%;
              }
            }
          }

          .content {
            flex: 4;
            margin-left: 60px;
            padding: 0;
            flex-wrap: nowrap;
            justify-content: inherit;
            height: 40vh;
            font-family: 'Proxima Nova';
            color: #525a5d;
            position: relative;

            @media @mobile {
              margin: 0;
              padding-top: 10px;
            }

            p {
              margin: 7px 0;

              @media @mobile {
                margin: 10px 0;
              }

              &:first-child {
                margin-top: 0;
              }

              b {
                color: black;
                font-style: italic;
              }
            }

            ol {
              list-style: none;
              margin: 0;

              li:before {
                @top-margin: 20px;
                margin: @top-margin 0;
                background: #b0d3de;
                content: ' ';
                width: 8px;
                height: 8px;
                display: inline-block;
                font-style: italic;
                border-radius: 50%;
                margin: @top-margin 10px 0 0; /*      */
              }
            }
          }
        }
      }

      .controls {
        display: flex;
        justify-content: center;

        @media @mobile {
          margin-top: 10px;
        }

        a.arrow {
          transition: color .2s linear;
          cursor: pointer;

          &:hover > img {
            filter: ~"grayscale(100%) brightness(0)";
          }
        }
      }
    }
  }
}
